:root {
    --color-main: #F23D4C;
    --color-secondary: #023E73;
    --color-main-alt: #D93D59;
    --color-secondary-alt: #35648C;
    --color-back: #f2f2f2;
}

body {
    font-family: 'IBM Plex Mono', monospace;
    background-color: var(--color-back);
    margin: 0;
    padding: 0;

}

#header {
    font-size: 1.5rem;
    text-align: center;
    color: var(--color-back);
    background-color: var(--color-secondary);
    border: 2px solid var(--color-secondary-alt);
    margin-bottom: 20px;
}

#content {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
    //border: 2px dotted black;

    .textBox {
        width: 600px;
        height: auto;
        //border: 2px dotted darkblue;
        div {
            padding: 0;
        }

        .textarea {
            background-color: var(--color-back);
            border: 2px solid var(--color-main);
            width: 100%;
            height: 400px;

        }
    }
}